import {
  HTMLAttributes,
  useCallback,
  useEffect,
  useState,
} from 'preact/compat';
import { Container, Slot, SlotProps } from '@adobe-commerce/elsie/lib';
import { CartModel } from '@/cart/data/models';
import { getPersistedCartData } from '@/cart/lib/persisted-data';
import { events } from '@adobe-commerce/event-bus';
import { MiniCart as MiniCartComponent } from '@/cart/components';
import { CartSummaryList } from '@/cart/containers';
import { useText } from '@adobe-commerce/elsie/i18n';
import { Button, ImageProps, Price } from '@adobe-commerce/elsie/components';
import { updateProductsFromCart } from '@/cart/api';
import { publishInitiateCheckoutEvent } from '@/cart/lib/acdl';
import { state } from '@/cart/lib/state';

export interface MiniCartProps extends HTMLAttributes<HTMLDivElement> {
  routeProduct?: (item: CartModel['items'][0]) => string;
  routeCart?: () => string;
  routeCheckout?: () => string;
  routeEmptyCartCTA?: () => string;
  slots?: {
    ProductList?: SlotProps;
    ProductListFooter?: SlotProps;
    PreCheckoutSection?: SlotProps;
    Thumbnail?: SlotProps<{ 
      item: CartModel['items'][number], 
      defaultImageProps: ImageProps 
    }>;
  };
  hideFooter?: boolean;
  displayAllItems?: boolean;
  showDiscount?: boolean;
  showSavings?: boolean;
  enableItemRemoval?: boolean;
  enableQuantityUpdate?: boolean;
  hideHeading?: boolean;
}

export const MiniCart: Container<MiniCartProps, CartModel | null> = ({
  children,
  initialData = null,
  hideFooter = true, // Default to true for MiniCart
  slots,
  routeProduct,
  routeCart,
  routeCheckout,
  routeEmptyCartCTA,
  showDiscount,
  showSavings,
  enableItemRemoval = true, // Default to true for MiniCart
  enableQuantityUpdate = false, // Default to false for MiniCart
  hideHeading = false, // Default to false for MiniCart
  ...props
}) => {
  const [data, setData] = useState<CartModel | null>(initialData);
  const cartTaxesConfig = state.config?.shoppingCartDisplaySetting;

  useEffect(() => {
    const event = events.on(
      'cart/data',
      (payload) => {
        setData(payload as CartModel);
      },
      { eager: true }
    );

    return () => {
      event?.off();
    };
  }, []);

  const dictionary = useText({
    cartLink: 'Cart.MiniCart.cartLink',
    checkoutLink: 'Cart.MiniCart.checkoutLink',
  });

  const handleItemQuantityUpdate = (uid: string, quantity: number) => {
    return updateProductsFromCart([{ uid, quantity }]);
  };

  const handleItemRemove = (uid: string) => handleItemQuantityUpdate(uid, 0);

  const checkoutLinkDisabled = data?.hasOutOfStockItems;

  const handleInitiateCheckout = useCallback(() => {
    if (data && !checkoutLinkDisabled) {
      publishInitiateCheckoutEvent(data, state.locale);
    }
  }, [data, checkoutLinkDisabled]);

  const productList = (
    <Slot
      name="ProductList"
      slot={slots?.ProductList}
      context={{
        itemQuantityUpdateHandler: handleItemQuantityUpdate,
        itemRemoveHandler: handleItemRemove,
        totalQuantity: data?.totalQuantity,
      }}
    >
      <CartSummaryList
        data-testid="default-cart-summary-list"
        routeProduct={routeProduct}
        routeEmptyCartCTA={routeEmptyCartCTA}
        initialData={data}
        maxItems={state.config?.miniCartMaxItemsDisplay}
        showMaxItems={!!state.config?.miniCartMaxItemsDisplay}
        hideHeading={hideHeading || !data?.totalQuantity}
        hideFooter={hideFooter}
        enableRemoveItem={enableItemRemoval}
        enableUpdateItemQuantity={enableQuantityUpdate}
        showDiscount={showDiscount}
        showSavings={showSavings}
        slots={{
          Thumbnail: slots?.Thumbnail,
        }}
      />
    </Slot>
  );

  const getProductListFooter = (data: CartModel | null) => {
    return (
      <Slot
        name="ProductListFooter"
        slot={slots?.ProductListFooter}
        context={{
          data,
        }}
      />
    );
  };

  const getPreCheckoutSection = (data: CartModel | null) => {
    return (
      <Slot
        name="PreCheckoutSection"
        slot={slots?.PreCheckoutSection}
        context={{
          data,
        }}
      />
    );
  };

  const getSubtotalPriceProps = () =>
    cartTaxesConfig?.subtotal === 'INCLUDING_TAX' ||
    cartTaxesConfig?.subtotal === 'INCLUDING_EXCLUDING_TAX'
      ? {
          amount: data?.subtotal.includingTax.value,
          currency: data?.subtotal.includingTax.currency,
          'data-testid': 'subtotal-including-tax',
          style: { font: 'inherit' },
        }
      : {
          amount: data?.subtotal.excludingTax.value,
          currency: data?.subtotal.excludingTax.currency,
          'data-testid': 'subtotal-excluding-tax',
          style: { font: 'inherit' },
        };

  return (
    <MiniCartComponent
      {...props}
      productListFooter={getProductListFooter(data)}
      subtotal={
        !data?.totalQuantity
          ? undefined
          : data?.subtotal && <Price {...getSubtotalPriceProps()} />
      }
      subtotalExcludingTaxes={
        !data?.totalQuantity
          ? undefined
          : data?.subtotal &&
            (cartTaxesConfig?.subtotal === 'INCLUDING_EXCLUDING_TAX' ? (
              <Price
                amount={data?.subtotal.excludingTax.value}
                currency={data?.subtotal.excludingTax.currency}
                data-testid="subtotal-including-excluding-tax"
                style={{ font: 'inherit' }}
              />
            ) : undefined)
      }
      preCheckoutSection={getPreCheckoutSection(data)}
      ctas={
        !data?.totalQuantity ? undefined : (
          <div>
            {routeCheckout && (
              <Button
                data-testid="route-checkout-button"
                variant="primary"
                href={checkoutLinkDisabled ? undefined : routeCheckout()}
                disabled={checkoutLinkDisabled}
                aria-disabled={checkoutLinkDisabled}
                onClick={handleInitiateCheckout}
              >
                {dictionary.checkoutLink}
              </Button>
            )}
            {routeCart && (
              <Button
                data-testid="route-cart-button"
                variant="tertiary"
                href={routeCart()}
              >
                {dictionary.cartLink}
              </Button>
            )}
          </div>
        )
      }
      products={productList}
    />
  );
};

MiniCart.getInitialData = async function () {
  return getPersistedCartData();
};
